#board{position:absolute}.top{top:0}.left{left:0}.right{right:0}.bottom{bottom:0}.center{left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.board{display:inline-table;border-collapse:collapse}.board td{text-align:center;font-weight:600;-webkit-transition:all 0.2s ease-in-out;transition:all 0.2s ease-in-out}.board td.noinitial{cursor:pointer}.board td.noinitial:hover{background-color:green;-webkit-transform:scale(1.3);transform:scale(1.3)}.board td.noinitial.focus{background-color:red;-webkit-transform:scale(1);transform:scale(1)}.hide_input{visibility:hidden}



